@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500&display=swap");
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: "Ubuntu", sans-serif;
  font-weight: 500;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.container {
  width: 420px;
}
.container .card .card-header {
  padding: 9px 18px 9px 18px;
  min-height: 100px;
}
.container .card .card-body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.container .card .card-body .number {
  font-size: 30px;
  width: 80px;
  height: 80px;
  margin: 5px;
  display: grid;
  place-items: center;
}
.container .card .card-body .large {
  width: 170px;
}
.container .card .card-header .Screen {
  font-size: 65px;
}
.container .card .card-header .miniScreen {
  padding: 0 3px;
  margin: 0;
  color: #02841a;
  font-size: 30px;
}
.container .card .card-body .btn:active {
  transform: scale(0.95);
  opacity: 0.7;
}
.container .card .card-body .btn:focus {
  outline: none;
  box-shadow: none;
}
.container .card .card-body span {
  font-size: 40px;
}
.container .card .card-body .btn span {
  color: rgb(255, 255, 255);
}
@media only screen and (min-width: 250px) and (max-width: 385px) {
  .container {
    width: 340px;
  }
  .container .card .card-body .number.number {
    font-size: 20px;
    width: 60px;
    height: 60px;
    margin: 5px;
    display: grid;
    place-items: center;
  }
  .container .card .card-body .large {
    min-width: 130px;
  }
  .container .card .card-header .Screen {
    font-size: 50px;
  }
  .container .card .card-header .miniScreen {
    font-size: 25px;
  }
  .container .card .card-header {
    min-height: 110px;
  }
  .container .card .card-body span {
    font-size: 30px;
  }
}
